<template>
  <div class="article">
    <div v-if="isloading">
      <img src="../assets/loading.gif" alt="" />
    </div>
    <div v-else>
      <div class="topic_header">
        <div class="topic_title">
          {{ post.title }}
        </div>
        <ul>
          <li>•发布于:{{ post.create_at | formatDate }}</li>

          <li>•作者:{{ post.author.loginname }}</li>

          <li>•{{ post.visit_count }}次浏览</li>
          <li>•来自{{ post | tabFormat }}</li>
        </ul>
        <div v-html="post.content" class="topic_conent"></div>
      </div>

      <div id="reply">
        <div class="topbar">回复</div>
        <div v-for="(reply, index) in post.replies" class="replySec">
          <div class="replyUp">
            <router-link
              :to="{
                name: 'user_info',
                params: {
                  name: reply.author.loginname,
                },
              }"
            >
              <img :src="reply.author.avatar_url" alt="" />
            </router-link>
            <span>{{ reply.author.loginname }}</span>
            <!-- 第几楼 -->
            <span>{{ index + 1 }}楼</span>
            <!-- 多少人顶 -->
            <span v-if="reply.ups.length > 0">
              {{ reply.ups.length }}
            </span>
            <!-- 没有人顶就什么都不显示 -->
            <span v-else></span>
          </div>
          <!-- 评论内容 -->
          <p v-html="reply.content"></p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Article",
  data() {
    return {
      isloading: false,
      post: {}, //空对象 保存返回的结果（当前文章页的所有数据）
    };
  },
  methods: {
    getArticleData() {
      this.$axios
        .get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
        .then((res) => {
          this.isloading = false;
          if (res.data.success === true) {
            this.post = res.data.data;
            console.log(this.post);
          }
        })
        .catch((err) => {
          console.log("err");
          console.log(err);
        });
    },
  },
  beforeMount() {
    this.isloading = true;
    this.getArticleData();
  },
  //检测路由是否发生变化
  watch: {
    $route(to, from) {
      this.getArticleData();
    },
  },
};
</script>
<style >
@import url("../assets/markdown-github.css");
body {
  background-color: #e1e1e1;
}
.topbar {
  padding: 10px;
  background-color: #f6f6f6;
  height: 16px;
  font-size: 12px;
  margin-top: 15px;
}
.article:not(:first-child) {
  margin-right: 340px;
  margin-top: 15px;
}
#reply,
.topic_header {
  background-color: #fff;
}
#reply {
  margin-top: 15px;
}
#reply img {
  width: 30px;
  height: 100%;

  position: relative;
  bottom: -9px;
  cursor: pointer;
}
#reply a,
#reply span {
  font-size: 13px;
  color: #666;
  text-decoration: none;
}
.replySec {
  border-bottom: 1px solid #e5e5e5;
}
.isloading {
  text-align: center;
  padding-top: 300px;
}
.replyUp a:nth-of-type(2) {
  margin-left: 0px;
  display: inline-block;
}

.topic_header {
  padding: 10px;
}
.topic_title {
  font-size: 20px;
  font-weight: bold;
  padding-top: 8px;
}
.topic_header ul {
  list-style-type: none;
  padding: 0px 0px;
  margin: 6px 0px;
}
.topic_header li {
  display: inline-block;
  font-size: 12px;
  color: #838383;
}

.topic_content {
  border-top: 1px solid #e5e5e5;
  padding: 0 10px;
}
.markdown-text img {
  width: 70% !important;
}
</style>



