<template>
  <div id="App">
    <Header></Header>
    <main>
      <!-- SideBar设置了向右浮动所有放在上面 -->
      <router-view name="sidebar"></router-view>
      <router-view name="main"></router-view>
    </main>
  </div>
</template>

<script>
import Header from "./components/Header.vue";

export default {
  name: "App",
  components: {
    Header,
  },
};
</script>
<style>
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
main {
  width: 80%;
  margin: 0 auto;
}
</style>



