<template>
  <div class="pagination">
    <button @click="changePage">首页</button>
    <!-- changePage不加()传递的是原生事件 -->
    <button @click="changePage">上一页</button>
    <button class="pagebtn" v-if="flag">...</button>
    <button
      v-for="btn in btnPages"
      :class="[{ currentPage: btn === currentPage }, 'pagebtn']"
      @click="changePage(btn)"
    >
      {{ btn }}
    </button>
    <button @click="changePage">下一页</button>
  </div>
</template>

<script>
import $ from "jquery";
console.log($);
export default {
  name: "Pagination",
  data() {
    return {
      btnPages: [1, 2, 3, 4, 5, "..."],
      currentPage: 1,
      flag: false,
    };
  },
  methods: {
    changePage(page) {
      if (typeof page != "number") {
        switch (page.target.innerText) {
          case "上一页":
            $("button.currentPage").prev().click();
            break;
          case "下一页":
            $("button.currentPage").next().click();
            break;
          case "首页":
            this.btnPages = [1, 2, 3, 4, 5, "..."];
            this.changeBtn(1);
            break;
          default:
            break;
        }
        return;
      }

      this.currentPage = page;
      if (page > 4) {
        this.flag = true;
      } else {
        this.flag = false;
      }
      if (page === this.btnPages[4]) {
        //删除第一个
        this.btnPages.shift();
        //在最后添加一个
        this.btnPages.splice(4, 0, this.btnPages[3] + 1);
      } else if (page === this.btnPages[0] && page != 1) {
        //在第一个添加
        this.btnPages.splice(0, 0, this.btnPages[0] - 1);
        //删除最后一个
        this.btnPages.splice(5, 1);
      }
      //子组件向父组件发送数据
      this.$emit("handleList", this.currentPage);
    },
  },
};
</script>
<style scoped>
.pagination {
  margin-top: 5px;
  margin-bottom: 20px;
  background-color: white;
  padding: 6px 20px;
  border-radius: 5px;
  /*box-shadow: 0px 2px 9px #888888;*/
  border: 1px solid #888888;
}

button {
  background-color: #fff;
  border: 1px solid #ddd;
  color: #778087;
  border-radius: 3px;
  outline: none;
  height: 21px;
  cursor: pointer;
  padding: 0 2px;
  width: 55px;
  height: 29px;
}

.pagebtn {
  position: relative;
  bottom: 1px;
  width: 40px;
  margin: 0 4px;
}

.currentPage {
  color: white;
  background-color: #1f1b1b;
}
</style>


