<template>
  <div class="header">
    <router-link :to="{ name: 'root' }">
      <img src="../assets/cnodejs_light.svg" alt="" />
    </router-link>
    <ul>
      <li><a href="#"></a>首页</li>
      <li><a href="#"></a>新手</li>
      <li><a href="#"></a>API</li>
      <li><a href="#"></a>关于</li>
      <li><a href="#"></a>注册</li>
      <li><a href="#"></a>登录</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Header",
};
</script>
 


<style scoped>
/* scoped 样式针对当前组件有效 不加就对整个文件有效 */
* {
  margin: 0;
  padding: 0;
}
.header {
  background-color: #444444;
  height: 50px;
}
img {
  max-width: 120px;
  margin-left: 50px;
  margin-top: 10px;
}
ul {
  list-style: none;
  float: right;
  margin-top: 10px;
}
li {
  display: inline-block;
  color: #ccc;
  padding: 5px 10px;
}
</style>



