import Vue from 'vue'
import Router from 'vue-router'
import Article from '../components/Article'
import PostList from '../components/PostList'
import userInfo from '../components/userInfo'
import SideBar from '../components/SideBar'
import Ask from '../components/Ask'
import Share from '../components/share'
import Good from '../components/Good'
import Job from '../components/Job'
Vue.use(Router)

export default new Router({
  routes: [
    //根路由
    {
      name: 'root',
      path: '/',
      // path指向的组件
      components: {
        main: PostList,
      }
    },
    // 文章页面路由
    {
      name: 'post_content',
      path: '/topic/:id&author=:name',
      // path指向的组件
      components: {
        main: Article,
        sidebar: SideBar
      }
    },
    //用户信息路由
    {
      name: 'user_info',
      path: '/userinfo/:name',
      //path指向的组件
      components: {
        main: userInfo,
      }
    },
    // 问答页面路由
    {
      name: 'askList',
      path: '/?tab=:tab',
      // path指向的组件
      components: {
        main: Ask,
      }
    },
    // 分享页面路由
    {
      name: 'shareList',
      path: '/?tab=:tab',
      // path指向的组件
      components: {
        main: Share,
      }
    },
    // 精华页面路由
    {
      name: 'goodList',
      path: '/?tab=:tab',
      // path指向的组件
      components: {
        main: Good,
      }
    },
    // 工作页面路由
    {
      name: 'jobList',
      path: '/?tab=:tab',
      // path指向的组件
      components: {
        main: Job,
      }
    },
  ]
})



