<template>
  <div class="PostList">
    <!-- 正在加载时 -->
    <div class="isloading" v-if="isloading">
      <img src="../assets/loading.gif" alt="" />
    </div>
    <!-- 加载成功后 帖子列表 -->
    <div class="posts" v-else>
      <ul>
        <li>
          <div class="topbar">
            <router-link
              :to="{
                name: 'root',
              }"
            >
              <span>全部</span>
            </router-link>
            <router-link
              :to="{
                name: 'goodList',
                params: {
                  tab: 'good',
                },
              }"
            >
              <span>精华</span>
            </router-link>
            <router-link
              :to="{
                name: 'shareList',
                params: {
                  tab: 'share',
                },
              }"
            >
              <span>分享</span>
            </router-link>
            <router-link
              :to="{
                name: 'askList',
                params: {
                  tab: 'ask',
                },
              }"
            >
              <span>问答</span>
            </router-link>
            <router-link
              :to="{
                name: 'jobList',
                params: {
                  tab: 'job',
                },
              }"
            >
              <span>招聘</span>
            </router-link>
          </div>
        </li>

        <li v-for="post in posts">
          <router-link
            :to="{
              name: 'user_info',
              params: {
                name: post.author.loginname,
              },
            }"
          >
            <img :src="post.author.avatar_url" alt="" />
          </router-link>
          <!-- 回复/浏览 -->
          <span class="allcount">
            <span class="reply_count">{{ post.reply_count }}</span>
            /{{ post.visit_count }}
          </span>
          <!-- 帖子分类 -->
          <span
            :class="[
              {
                put_good: post.good === true,
                put_top: post.top === true,
                'topiclist-tab': post.good != true && post.top != true,
              },
            ]"
          >
            {{ post | tabFormat }}
          </span>
          <!-- 标题 -->
          <router-link
            :to="{
              name: 'post_content', //请求路由的名字
              params: {
                //路由参数
                id: post.id, //id
                name: post.author.loginname,
              },
            }"
          >
            <span class="title">
              {{ post.title }}
            </span>
          </router-link>

          <!-- 最后回复时间 -->
          <span class="last_replyTime">{{
            post.last_reply_at | formatDate
          }}</span>
        </li>
        <li>
          <!-- 子组件向父子间传递数据 -->
          <!-- handleList事件(在子组件$emit使用) renderList方法（在父组件methods里定义） -->
          <pagination @handleList="renderList"></pagination>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import pagination from "./Pagination.vue";

export default {
  name: "PostList",
  data() {
    return {
      isloading: false,
      posts: [], //保存数据
      postpage: 1,
    };
  },
  components: {
    pagination,
  },
  methods: {
    getData() {
      this.$axios
        .get(" https://cnodejs.org/api/v1/topics", {
          params: {
            page: this.postpage, //加载页数
            limit: 20, //每页的限制
          },
        })
        .then((res) => {
          //返回成功
          this.isloading = false;
          console.log(res);
          this.posts = res.data.data;
        })
        .catch((err) => {
          //处理返回失败后的问题
          console.log("error");
        });
    },
    renderList(value) {
      console.log(value);
      //value就是子组件的数据
      this.postpage = value;
      this.getData();
    },
  },
  //在页面挂载之前
  beforeMount() {
    (this.isloading = true), this.getData();
  },
};
</script>
<style scoped>
.PostList {
  background-color: #e1e1e1;
}

.posts {
  margin-top: 10px;
}

.PostList img {
  height: 30px;
  width: 30px;
  vertical-align: middle;
}

ul {
  list-style: none;
  width: 100%;
  max-width: 1344px;
  margin: 0 auto;
}

ul li:not(:first-child) {
  padding: 9px;
  font-size: 15px;
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma,
    "Hiragino Sans GB", STHeiti, sans-serif !important;
  font-weight: 400;
  background-color: white;
  color: #333;
  border-top: 1px solid #f0f0f0;
}

li:not(:first-child):hover {
  background-color: #f5f5f5;
}
li:last-child:hover {
  background: white;
}
li span {
  line-height: 30px;
}

.allcount {
  width: 80px;
  display: inline-block;
  text-align: center;
  font-size: 12px;
}

.reply_count {
  color: #9e78c0;
  font-size: 14px;
}

.put_good,
.put_top {
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  /* border-radius：用这个属性能实现圆角边框的效果。现在只有Mozilla/Firefox 和 Safari 3支持该属性。
-webkit-border-radius：苹果；谷歌，等一些浏览器认，因为他们都用的是webkit内核；
-moz-border-radius：moz这个属性 主要是专门支持Mozilla Firefox 火狐浏览器的CSS属性。 */
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  color: #fff;
  font-size: 12px;
  margin-right: 10px;
}

.topiclist-tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  font-size: 12px;
  margin-right: 10px;
}

.last_replyTime {
  text-align: right;
  /* display: inline-block; */
  float: right;
  min-width: 50px;
  white-space: nowrap;
  color: #778087;
  font-size: 12px;
}

.topbar {
  height: 40px;
  background-color: #f5f5f5;
}
.topbar span {
  font-size: 14px;
  color: #80bd01;
  line-height: 40px;
  margin: 0 10px;
  cursor: pointer;
}
.topbar span:hover {
  color: #9e78c0;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  text-decoration: underline;
}

.loading {
  text-align: center;
  padding-top: 300px;
}
</style>


